.container {
    width: 100%;
    height: 100%;
    position: relative;
}

.container:hover {
    cursor: pointer;
}

.carouselItem, .carouselItemImage {
    width: 100%;
    height: 100%;
}

.carouselItemImage > img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.carouselItemInfo {
    background: lightgrey;
    opacity: 0.5;
    color: green;
    font-size: 20px;
}

.carouselItemInfo > p {
    opacity: 1;
}

.movieInfoContainer {
    width: 500px;
    height: 200px;
    background: red;
    position: absolute;
}